.b-input {
	margin-bottom: 23px;
	&__label {
		font-weight: 600;
		margin-bottom: 12px; }

	&__input-row {
		position: relative;
		margin-bottom: 12px;
		&:last-child {
			margin-bottom: 0; } }

	&__right-button {
		position: absolute;
		right: 13px;
		top: 50%;
		@include translate(0, -50%); }

	&__icon-location {
		font-size: 20px; }

	&__input-icon {
		padding-right: 40px; }



	@media screen and (max-width: $max-lg) {
		&--hidden-mobile {
			display: none;
			&.js-active {
				display: block; } }
		&__label {
			&--hidden-mobile {
				display: none; } } } }
