.e-burger {
	display: flex;
	align-items: center;
	justify-content: center;

	position: absolute;

	top: -24px;
	right: -20px;

	width: 72px;
	height: 72px;
	background-color: $color-accent;
	@include border-radius(38px);
	@include transition(all, .35s, ease);
	&__line {
		position: relative;
		height: 2px;
		width: 24px;
		background-color: $color-dark;
		@include transition(all, .35s, ease);
		&:before,
		&:after {
			content: '';
			position: absolute;
			display: block;
			height: 2px;
			width: 12px;
			background-color: $color-dark;
			@include transition(all, .35s, ease); }

		&:before {
			top: -8px;
			right: 0; }
		&:after {
			bottom: -8px;
			left: 0; } }
	&.js-active {
		background-color: #ffffff;
		.e-burger__line {
			@include rotate(-45deg);
			&:before,
			&:after {
				width: 8px; }

			&:before {
				right: 8px; }
			&:after {
				left: 8px; } } } }
