
//Color
$color-accent: #f7da5f;
$color-font: #47443d;
$color-dark: #000000;
$color-light-bg: #e6eaee;
$color-silver: #595d5f;
$color-green: #40ba87;
$color-turquoise: #30646b;
$color-orange: #c68a70;
$color-brown: #b8513d;
//Font
$font-default: 'Prompt', Helvetica, Arial, sans-serif;
$font-additional: 'Alfa Slab One', Helvetica, Arial, sans-serif;
$font-bio: 'BioRhyme', Helvetica, Arial, sans-serif;
$font-cho: 'Chonburi', Helvetica, Arial, sans-serif;

//Media queries
$xs: 481px;
$sm: 576px;
$md: 993px;
$lg: 1025px;
$xl: 1280px;



$max-sm: $sm - 1px;
$max-lg: $lg - 1px;
$max-xl: $xl - 1px;


