.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  fill: currentColor;
}

.icon-cart {
  font-size:(21/10)*1rem;
  width:(19/21)*1em;
}
.icon-fullscreen {
  font-size:(1000/10)*1rem;
  width:(1000/1000)*1em;
}
.icon-location {
  font-size:(20/10)*1rem;
  width:(20/20)*1em;
}
.icon-logo {
  font-size:(32/10)*1rem;
  width:(26/32)*1em;
}
