.b-editor-map-desc-style-2 {
	position: relative;
	background-color: #ffffff;
	text-align: center;
	width: 100%;
	max-width: 100%;
	margin: 0 auto;
	padding: 8px 10px;


	&__city {
		font: 20px $font-additional;
		color: $color-turquoise;
		margin-bottom: 5px; }
	&__location {
		font-size: 10px;
		font-weight: 300;
		letter-spacing: 2px;
		text-transform: uppercase;
		color: $color-orange; }


	@media screen and (min-width: $sm) {
		width: 380px;
		padding: 30px 10px;
		@include border-radius(30px);
		&:before {
			content: '';
			position: absolute;
			top: 50%;
			right: 99%;
			width: 0;
			height: 0;
			border-top: 50px solid transparent;
			border-right: 20px solid #ffffff;
			border-bottom: 50px solid transparent;
			@include translate(0, -50%); }
		&:after {
			content: '';
			position: absolute;
			top: 50%;
			left: 99%;
			width: 0;
			height: 0;
			border-top: 50px solid transparent;
			border-left: 20px solid #ffffff;
			border-bottom: 50px solid transparent;
			@include translate(0, -50%); }
		&__city {
			font-size: 36px; }
		&__location {
			font-size: 14px; } } }
