.c-page {
	display: flex;
	flex-direction: column;
	height: 100%;
	overflow-x: hidden;
	&.js-mobile-menu {
		overflow: hidden; } }
.c-main {
	flex: 1 0 auto;

	&--full-height {
		height: calc(100% - 48px); } }

.c-header {
	position: relative;
	//overflow-x: hidden
	display: flex;
	align-items: center;
	flex-shrink: 0;
	padding: 8px;
	background-color: #ffffff;


	&__logo {
		position: relative;
		z-index: 10;
		padding-right: 12px; }

	&__navigation {
		padding-right: 12px;
		padding-left: 12px; }

	&__right {
		position: relative;
		z-index: 10;
		display: flex;
		align-items: center;
		margin-left: auto;
		padding-left: 12px; }

	&__button-corner {
		display: none; }

	&__burger {
		width: 48px; }
	&__create-button-mobile {
		margin: 105px -15px 0 -10px;
		text-align: center; } }

.c-content {
	padding: 50px 0 50px; }


.c-footer {
	padding: 30px 0px;
	flex: 0 0 auto;
 }	//background-color: $footer-background

.main-bold {
	font-weight: 600; }

@media screen and (max-width: $max-lg) {

	.c-header {
		&__navigation {
			//display: none
			position: fixed;
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			background-color: $color-accent;
			z-index: 5;
			overflow-y: auto;

			padding: 150px 48px 0;
			opacity: 0;
			visibility: hidden;
			@include scale(.2);
			@include transition(all, .4s, ease);

			&.js-active {
				opacity: 1;
				visibility: visible;
				@include scale(1); } } } }



@media screen and (min-width: $lg) {

	.c-header {
		&:after {
			content: '';
			position: absolute;
			left: 0;
			right: 0;
			top: 100%;
			display: block;
			width: 100%;
			height: 4px;
			background-image: linear-gradient(to top, rgba(230, 234, 238, 0) 0%, #e6eaee 100%); }
		&__button-corner {
			margin-top: -9px;
			display: block; }

		&__burger {
			display: none; }
		&__create-button-mobile {
			display: none; } } }
