.b-radio-tile-select {

	display: flex;
	align-items: center;
	flex-wrap: wrap;

	&__item {
		flex: 1;
		text-align: center; }


	&__label {
		font-size: 11px; }

	&__preview {
		width: 42px;
		height: 42px;
		background-color: #ffffff;
		padding: 1px;
		box-shadow: inset 0 0 0px 2px #ffffff;
		border: 2px solid #dde0e2;
		margin: 0 auto;
		@include border-radius(1.6em);
		background-repeat: no-repeat;
		background-size: cover;
		background-position: center; }

	&__input {
		display: none;
		&:checked + .b-radio-tile-select__preview {
			border-color: $color-green; }
		&:checked ~ span {
			color: $color-green;
			font-weight: 600; } } }
