.b-radio-select {
	&__list {
		display: flex;
		align-items: center;
		flex-wrap: wrap;
		border: 1px solid #e6eaee;
		@include border-radius(6px); }
	&__item {
		position: relative;
		flex: 1;
		text-align: center;
		border-right: 1px solid #e6eaee;
		padding: 8px 5px;
		&:last-child {
			border: none; } }
	&__bg {
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: #e6eaee;
		opacity: .25; }
	&__input {
		display: none;
		&:checked + span {
			color: $color-green;
			font-weight: 600; }
		&:checked ~ .b-radio-select__bg {
			background-color: #ffffff; } }

	&__label {
		font-size: 11px; }
	&__add-price {
		color: #262a2c;
		margin-left: 5px;
		opacity: .55; }

	@media screen and (min-width: $sm) {
		&__item {
			padding: 8px 10px; } } }
