body,
html {
	height: 100%;
	margin: 0; }
body {
	font: 16px/1.4 $font-default;
	color: $color-dark;

	* {
		box-sizing: border-box;
		outline: none;
		&:hover,
		&:focus {
			outline: none; } }

	&.js-mobile-menu {
		overflow: hidden; } }
a {
	text-decoration: none;
	color: $color-dark;
	@include transition(all, .4s, ease);
	&:hover,
	&:focus {
		color: $color-accent; } }

img {
	max-width: 100%;
	vertical-align: middle; }

p {
	font-size: 16px;
	margin-top: 0;
	margin-bottom: 15px; }

button, input, textarea, .e-input {
	@include transition(all, .4s, ease); }


input, textarea, .e-input {
	display: block;
	border-radius: 6px;
	border: 1px solid #e6eaee;
	width: 100%;
	font-size: 11px;
	padding: 12px 12px 11px;
	&.error, &:focus:required:invalid {
 }		//border-color: $input-error-border
	&:focus,
	&:required:valid {
		border-color: $color-accent; } }


input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {

	-webkit-appearance: none;
	-moz-appearance: textfield;
	margin: 0;
	padding: 0;
	width: 0;
	display: none; }

input[type="time"] {
	font-size: 13px;
	padding-right: 0; }
label {
	cursor: pointer; }
button {
	padding: 0;
	background-color: transparent;
	border: none;
	box-shadow: none;
	cursor: pointer; }
video {
	max-width: 100%; }
table {
	width: 100%; }

::selection {
	background-color: $color-accent;
	color: #fff;
	text-shadow: none; }

::placeholder {
	//font-family: $add-font
 }	//color: $color-font-light


ul {
	list-style: none;
	padding: 0;
	margin: 0; }
