body,
html {
  height: 100%;
  margin: 0;
}

body {
  font: 16px/1.4 "Prompt", Helvetica, Arial, sans-serif;
  color: #000000;
}

body * {
  box-sizing: border-box;
  outline: none;
}

body *:hover, body *:focus {
  outline: none;
}

body.js-mobile-menu {
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #000000;
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

a:hover, a:focus {
  color: #f7da5f;
}

img {
  max-width: 100%;
  vertical-align: middle;
}

p {
  font-size: 16px;
  margin-top: 0;
  margin-bottom: 15px;
}

button, input, textarea, .e-input {
  -webkit-transition: all 0.4s ease;
  -moz-transition: all 0.4s ease;
  -ms-transition: all 0.4s ease;
  -o-transition: all 0.4s ease;
  transition: all 0.4s ease;
}

input, textarea, .e-input {
  display: block;
  border-radius: 6px;
  border: 1px solid #e6eaee;
  width: 100%;
  font-size: 11px;
  padding: 12px 12px 11px;
}

input:focus, input:required:valid, textarea:focus, textarea:required:valid, .e-input:focus, .e-input:required:valid {
  border-color: #f7da5f;
}

input[type="time"]::-webkit-outer-spin-button,
input[type="time"]::-webkit-inner-spin-button,
input[type="time"]::-webkit-inner-spin-button {
  -webkit-appearance: none;
  -moz-appearance: textfield;
  margin: 0;
  padding: 0;
  width: 0;
  display: none;
}

input[type="time"] {
  font-size: 13px;
  padding-right: 0;
}

label {
  cursor: pointer;
}

button {
  padding: 0;
  background-color: transparent;
  border: none;
  box-shadow: none;
  cursor: pointer;
}

video {
  max-width: 100%;
}

table {
  width: 100%;
}

::selection {
  background-color: #f7da5f;
  color: #fff;
  text-shadow: none;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.c-page {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-x: hidden;
}

.c-page.js-mobile-menu {
  overflow: hidden;
}

.c-main {
  flex: 1 0 auto;
}

.c-main--full-height {
  height: calc(100% - 48px);
}

.c-header {
  position: relative;
  display: flex;
  align-items: center;
  flex-shrink: 0;
  padding: 8px;
  background-color: #ffffff;
}

.c-header__logo {
  position: relative;
  z-index: 10;
  padding-right: 12px;
}

.c-header__navigation {
  padding-right: 12px;
  padding-left: 12px;
}

.c-header__right {
  position: relative;
  z-index: 10;
  display: flex;
  align-items: center;
  margin-left: auto;
  padding-left: 12px;
}

.c-header__button-corner {
  display: none;
}

.c-header__burger {
  width: 48px;
}

.c-header__create-button-mobile {
  margin: 105px -15px 0 -10px;
  text-align: center;
}

.c-content {
  padding: 50px 0 50px;
}

.c-footer {
  padding: 30px 0px;
  flex: 0 0 auto;
}

.main-bold {
  font-weight: 600;
}

@media screen and (max-width: 1024px) {
  .c-header__navigation {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color: #f7da5f;
    z-index: 5;
    overflow-y: auto;
    padding: 150px 48px 0;
    opacity: 0;
    visibility: hidden;
    -webkit-transform: scale(0.2);
    -moz-transform: scale(0.2);
    -ms-transform: scale(0.2);
    transform: scale(0.2);
    -webkit-transition: all 0.4s ease;
    -moz-transition: all 0.4s ease;
    -ms-transition: all 0.4s ease;
    -o-transition: all 0.4s ease;
    transition: all 0.4s ease;
  }
  .c-header__navigation.js-active {
    opacity: 1;
    visibility: visible;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    transform: scale(1);
  }
}

@media screen and (min-width: 1025px) {
  .c-header:after {
    content: '';
    position: absolute;
    left: 0;
    right: 0;
    top: 100%;
    display: block;
    width: 100%;
    height: 4px;
    background-image: linear-gradient(to top, rgba(230, 234, 238, 0) 0%, #e6eaee 100%);
  }
  .c-header__button-corner {
    margin-top: -9px;
    display: block;
  }
  .c-header__burger {
    display: none;
  }
  .c-header__create-button-mobile {
    display: none;
  }
}

.e-button-corner {
  display: inline-block;
  position: relative;
  font-size: 14px;
  font-weight: 600;
  background-image: linear-gradient(-45deg, rgba(255, 255, 255, 0) 8px, rgba(255, 255, 255, 0), #f7da5f 0);
  padding: 15px 17px 5px;
  border-radius: 0 0 4px 4px;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.e-button-corner:before {
  content: '';
  position: absolute;
  right: 0;
  bottom: 0;
  width: 0;
  height: 0;
  border-top: 11px solid rgba(0, 0, 0, 0.2);
  border-right: 11px solid transparent;
}

.e-button-corner:hover, .e-button-corner:focus {
  color: #000000;
  opacity: .8;
}

.e-button-frame {
  display: inline-block;
  position: relative;
  font-size: 37px;
  font-weight: 300;
  padding: 5px 34px 36px 13px;
}

.e-button-frame__border {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-image: linear-gradient(-135deg, rgba(255, 255, 255, 0) 18px, rgba(255, 255, 255, 0), #000000 0);
  border-radius: 8px 8px 0 0;
  z-index: -1;
}

.e-button-frame:after {
  content: '';
  position: absolute;
  top: 2px;
  right: 2px;
  bottom: 0px;
  left: 2px;
  background-image: linear-gradient(-135deg, rgba(255, 255, 255, 0) 17px, rgba(255, 255, 255, 0), #f7da5f 0);
  z-index: 1;
  border-radius: 5px 5px 0 0;
  z-index: -1;
}

.e-button-frame__triangle {
  position: absolute;
  top: 2px;
  right: 2px;
  width: 23px;
  height: 23px;
  background-image: url(data:image/svg+xml;base64,);
  background-position: 0px -3px;
  background-repeat: no-repeat;
}

.e-button-frame:hover, .e-button-frame:focus {
  color: #000000;
  opacity: .8;
}

.e-burger {
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  top: -24px;
  right: -20px;
  width: 72px;
  height: 72px;
  background-color: #f7da5f;
  border-radius: 38px;
  -webkit-border-radius: 38px;
  -moz-border-radius: 38px;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.e-burger__line {
  position: relative;
  height: 2px;
  width: 24px;
  background-color: #000000;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.e-burger__line:before, .e-burger__line:after {
  content: '';
  position: absolute;
  display: block;
  height: 2px;
  width: 12px;
  background-color: #000000;
  -webkit-transition: all 0.35s ease;
  -moz-transition: all 0.35s ease;
  -ms-transition: all 0.35s ease;
  -o-transition: all 0.35s ease;
  transition: all 0.35s ease;
}

.e-burger__line:before {
  top: -8px;
  right: 0;
}

.e-burger__line:after {
  bottom: -8px;
  left: 0;
}

.e-burger.js-active {
  background-color: #ffffff;
}

.e-burger.js-active .e-burger__line {
  -webkit-transform: rotate(-45deg);
  -moz-transform: rotate(-45deg);
  -ms-transform: rotate(-45deg);
  transform: rotate(-45deg);
}

.e-burger.js-active .e-burger__line:before, .e-burger.js-active .e-burger__line:after {
  width: 8px;
}

.e-burger.js-active .e-burger__line:before {
  right: 8px;
}

.e-burger.js-active .e-burger__line:after {
  left: 8px;
}

